export default {
	onLoad(options) {
		this.resList = JSON.parse(decodeURIComponent(options.resList));
	},
	data(){
		return {
			resList:[],
		}
	},
	methods:{
		resClick(resName){
			const resData = JSON.stringify(resName);
			uni.reLaunch({
				url:"../main/main?resName="+resData
			});
		}
	}
}
